'use strict';
const wall = document.querySelector('.wall');
const height = document.body.clientHeight;
const width = document.body.clientWidth;
const wallHeight = wall.clientHeight + 20;
const wallWidth = wall.clientWidth + 20;
const wid = (width - wallWidth) / 2;
const heig = (height - wallHeight) / 2;


wall.addEventListener('click', e => {
  const item = e.target;
  const spider = document.querySelector('.spider');
  spider.style.left = (e.clientX - wid - spider.offsetWidth) + 'px';
  spider.style.top = (e.screenY - heig - spider.offsetWidth) + 'px';

  console.log(e.wall);
});

console.log(width);
